
import Vue from 'vue';
import Vuetify from 'vuetify'; // css material design vuetify
import Router from 'vue-router';
// each tab vue component
import Map from '../components/map.vue';
import Leaderboard from '../components/leaderboard.vue';
import Game from '../components/game.vue';
import Contact from '../components/about.vue';

Vue.use(Router); // Router  
Vue.use(Vuetify); // Vuetify   (!     )

// you can use array in this router to show multiple routes
// https://blog.storyg.co/vue-js-posts/todos-tutorial

export default new Router({
	routes: [
		{
			path: '/',
			name: 'Map',
			component: Map
		},
		{
			path: '/Leaderboard',
			name: 'Leaderboard',
			component: Leaderboard
		},
		{
			path: '/Game',
			name: 'Game',
			component: Game
		},
		{
			path: '/About',
			name: 'About',
			component: Contact
		},
	],
	mode: "history"
});
