<template>
    <div id="mapTab">
        <!-- Map API usage -->
        <div id="mapContainer">

            <!-- Actual Map -->
            <div id="map"></div>

            <!-- (+) button -->
            <v-btn
                class="mapBtn_plus"
                @click="pressBtnPlus"
                absolute
                dark
                fab
                right
                large
            >
                +
                <v-icon>mdi-plus</v-icon>
            </v-btn>
            <!-- (-) button -->
            <v-btn
                class="mapBtn_minus"
                @click="pressBtnMinus"
                absolute
                dark
                fab
                right
                large
            >
                -
                <v-icon>mdi-plus</v-icon>
            </v-btn>
            <!-- Get Here location Button -->
            <v-btn
                class="mapBtn_here"
                @click="pressBtnHere"
                absolute
                dark
                fab
                right
                large
            >
                Here
                <v-icon>mdi-plus</v-icon>
            </v-btn>
            <!-- Floating button -->
            <v-speed-dial
                v-model="fab.self"
                :top="fab.top"
                :bottom="fab.bottom"
                :left="fab.left"
                :direction="fab.direction"
                :open-on-hover="fab.hover"
                :transition="fab.transition"
            >
                <template v-slot:activator>
                    <v-btn
                        v-model="fab"
                        color="blue darken-2"
                        dark
                        fab
                        x-large
                    >
                        <v-icon v-if="fab">mdi-close</v-icon>
                        <v-icon v-else>mdi-account-circle</v-icon>
                    </v-btn>
                </template>
                <v-btn
                    fab
                    dark
                    small
                    color="green"
                    large
                >
                    <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                    fab
                    dark
                    small
                    color="indigo"
                    large
                >
                    <v-icon>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                    fab
                    dark
                    small
                    color="red"
                    large
                >
                    <v-icon>mdi-delete</v-icon>
                </v-btn>
            </v-speed-dial>
            <!-- Search bar -->
            <v-toolbar
                color="purple"
                dark
            >
                <v-text-field
                    class="mx-4"
                    flat
                    hide-details
                    label="Search"
                    solo-inverted
                ></v-text-field>
            </v-toolbar>
        </div>
    </div>
</template>

<script>
    import FCmap from '../class/map/FCmap.js';
    import FCwebStorage from '../class/storage/FCwebStorage.js';

    let tmpFcmap = new FCmap();
    let tmpFcws = new FCwebStorage();

    export default {
        name: 'Map',
        data: () => (
            {
                fab: {
                    self: false,
                    direction: 'left',
                    fab: false,
                    fling: true,
                    hover: false,
                    tabs: null,
                    top: false,
                    right: true,
                    bottom: true,
                    left: false,
                    transition: 'scale-transition'
                },
            }
        ),
        computed: {
            // activeFab () {
            //     console.log('activateFab function');
            //     switch (this.tabs) {
            //         case 'one': return { class: 'purple', icon: 'account_circle' }
            //         case 'two': return { class: 'red', icon: 'edit' }
            //         case 'three': return { class: 'green', icon: 'keyboard_arrow_up' }
            //         default: return {}
            //     }
            // }
        },
        mounted: function () {
            tmpFcmap.startUpdateLogic();
            tmpFcmap.mounted();
            tmpFcws.mounted();
            tmpFcmap.control.initLocation();
            
        },
        methods: {
            pressBtnPlus() {
                tmpFcmap.control.zoomIn();
            },
            pressBtnMinus() {
                tmpFcmap.control.zoomOut();
            },
            pressBtnHere() {
                tmpFcmap.control.getLocation();
            }
        }
    }
</script>

<style scoped>
    #mapTab {
        display: block;
        width: 100%;
        height: calc(100% - 80px);
    }
    /* plus, minus button style */
    .mapBtn_plus {
        /* position: absolute; */
        opacity: 0.65;
        right: 22px;
        bottom: 220px;
    }
    .mapBtn_minus {
        /* position: absolute; */
        opacity: 0.65;
        right: 22px;
        bottom: 150px;
    }
    /* Here location button style */
    .mapBtn_here {
        /* position: absolute; */
        opacity: 0.65;
        right: 22px;
        bottom: 80px;
    }
    /* action floating button style */
    .v-speed-dial {
        position: absolute;
        bottom: 72px;
        right: 100px;
        opacity: 0.75;
    }
    .v-btn--floating {
        position: relative;
    }
    #mapContainer {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
    }
    #mapContainer v-btn {
        position: absolute;
    }
    #map {
        position: relative;
        display: block;
        width: 100%;
        height: 100%;
    }
    .v-toolbar {
        position: relative;
        opacity: 0.45;
        bottom: 56px;
        left: 160px;
        width: calc(100% - 150px);
        z-index: 1;
    }

</style>