
// IMPORT Vue extension files
import Vue from 'vue' // Core Vue Framework
import Vuetify from 'vuetify'; // css material design vuetify
import 'vuetify/dist/vuetify.min.css'
import App from './App.vue' // import App.vue
import router from './router' // execute index.js in router folder

// production tip config (on build should be ignore)
Vue.config.productionTip = true;
Vue.config.silent = false;
Vue.config.devTools = true;

/* eslint-disable no-new */
var tmpVue = new Vue({
  el: '#app', // in index.html id="app" tag element
  vuetify: new Vuetify(),
  router,
  components: { App },
  template: '<App/>'
});
