
export default class FCmapControl {
    constructor(_mapClass) {
        this.mapClass = _mapClass;
        this.fcmap = undefined;
        this.self = undefined; // kakao.maps
        this.marker = null;
    }
    setfcmap(_fcmap, _self) {
        this.fcmap = _fcmap;
        this.self = _self;
    }
    // change map type between map or skyview
    // just example
    setMapType(_maptype) {
        var roadmapControl = document.getElementById('btnRoadmap');
        var skyviewControl = document.getElementById('btnSkyview'); 
        if (_maptype === 'roadmap') {
            map.setMapTypeId(kakao.maps.MapTypeId.ROADMAP);    
            roadmapControl.className = 'selected_btn';
            skyviewControl.className = 'btn';
        } else {
            map.setMapTypeId(kakao.maps.MapTypeId.HYBRID);    
            skyviewControl.className = 'selected_btn';
            roadmapControl.className = 'btn';
        }
    }
    zoomIn() {
        let tmpLevel = this.fcmap.getLevel();
        this.fcmap.setLevel(tmpLevel - 1);
    }
    zoomOut() {
        let tmpLevel = this.fcmap.getLevel();
        this.fcmap.setLevel(tmpLevel + 1);
    }

    // (HTML) get here location
    initLocation() {
        this.getLocation();
    }
    getLocation() {
        // if support GPS
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                // when success
                (_position) => {
                    this.mapClass.push2UA('SET_LOCATION', _position);
                },
                // when it errors
                function(error) {
                    console.error(error);
                },
                {
                    enableHighAccuracy: false,
                    maximumAge: 0,
                    timeout: Infinity
                }
            );
        }
        else {
            alert('NO GPS Support might change browser to latest');
        }
    }


    // (KAKAO) 
    setLocation(_position) {
        if (_position) {
            let tmpLat = _position.coords.latitude;
            let tmpLong = _position.coords.longitude;
            let tmpLatValue = new this.self.LatLng(tmpLat, tmpLong);
            this.fcmap.panTo(tmpLatValue);
            this.removeMarker();
            this.displayMarker(tmpLatValue);
        }
        else {
            console.log('Position Value is not exist');
        }
        return _position;
    }
    displayMarker(_tmpLatValue) {
        this.marker = new this.self.Marker({
            position: _tmpLatValue
        });
        this.marker.setMap(this.fcmap);
    }
    removeMarker() {
        try {
            this.marker.setMap(null);
        } catch(e) {}
    }
}
