
import FCmapControl from './FCmapControl.js';
import APIindex from './API/index.js';

// using kakao map api
export default class FCmap {
    constructor() {
        this.appKey = '5196ce531b985d6ae701ff6b64879fe2';
        this.URL = '//dapi.kakao.com/v2/maps/sdk.js?appkey=' + this.appKey;
        this.id = "map";
        this.self = this.initSelf();
        this.level = this.initLevel();
        this.pos = undefined;
        this.mapOption = undefined;
        this.map = undefined;
        this.isQueueCleared = true;
        this.updateArr = [];
        this.delay = 100;

        // MAP CONTROL
        this.control = new FCmapControl(this);

        // PUBLIC API CLASS
        this.API = new APIindex();
    }
    mounted(_pos) {
        this.pos = this.mountedPos(_pos);
        this.container = this.mountedContainer(this.id);
        this.mapOption = this.mountedMapOption(this.self, this.pos, this.level);
        this.map = this.mountedMap(this.self, this.container, this.mapOption);
        this.control.setfcmap(this.map, this.self);
    }
    update() {
        this.updateCmdList();
        // this.
    }
    initSelf() {
        return kakao.maps;
    }
    initLevel() {
        let tmpL = 5;
        return tmpL;
    }


    mountedPos(_pos) {
        let tmpP = {};
        if (_pos) {
            tmpP.x = _pos.x;
            tmpP.y = _pox.y;
        }
        else {
            tmpP.x = 33.450701;
            tmpP.y = 126.570667;
        }
        return tmpP;
    }
    mountedContainer(_id) {
        let tmpC = document.getElementById(_id);
        return tmpC;
    }
    mountedMapOption(_self, _pos, _level) {
        let tmpMO = {};
        tmpMO.center = new _self.LatLng(_pos.x, _pos.y);
        tmpMO.level = _level;
        // access wheel draggable, mobile touch
        tmpMO.draggable = true;
        tmpMO.scrollwheel = true;
        return tmpMO;
    }
    mountedMap(_self, _container, _mapOption) {
        return new _self.Map(_container, _mapOption);
    }

    // push to update array logic
    push2UA(_cmd, _data) {
        this.updateArr.push({
            cmd: _cmd,
            data: _data
        });
    }
    popFromUA() {
        return this.updateArr.pop();
    }

    // control update Queue
    lockQueue() {
        this.isQueueCleared = false;
    }
    unLockQueueAfterDelay() {
        setTimeout(
            () => { this.isQueueCleared = true; }, 1000
        );
    }
    chckIsQueueCleared() {
        return this.isQueueCleared;
    }
    // update function logic
    startUpdateLogic() { // scope should be window object
        let tmpUpdate = this.update.bind(this);
        if (window.FCUpdateLogic) {
            clearInterval(window.FCUpdateLogic);
        } else {}
        window.FCUpdateLogic = setInterval(
            tmpUpdate,
            this.delay
        );
    }
    updateCmdList() {
        if (this.updateArr.length > 0) {
            let tmpPop = this.popFromUA();
            if (this.chckIsQueueCleared()) {
                this.lockQueue();
                this.unLockQueueAfterDelay();
                let tmpCmd = tmpPop.cmd;
                console.log('Get update command function:', tmpPop.cmd);
                switch (tmpCmd) {
                    case 'SET_LOCATION':
                        this.control.setLocation(tmpPop.data);
                    break;
                    case 'GET_LOCATION':
                    break;
                    default:
                        console.log(tmpPop, '<= this is not on the list');
                    break;
                }
            } else {}
        }
    }
}